
html {
  body {
    .with_frm_style {
      input[type='text'],
      input[type='email'],
      input[type='url'],
      input[type='password'],
      input[type='search'],
      input[type='number'],
      input[type='tel'],
      input[type='range'],
      input[type='date'],
      input[type='month'],
      input[type='week'],
      input[type='time'],
      input[type='datetime'],
      input[type='datetime-local'],
      input[type='color'],
      textarea {
        border: 1px solid $secondary-color;
        border-radius: 10px;
        font-size: $font-size-base;
        line-height: 1em;
        padding: 15px;
        vertical-align: middle;
      }
      input[type='text'],
      input[type='email'],
      input[type='url'],
      input[type='password'],
      input[type='search'],
      input[type='number'],
      input[type='tel'],
      input[type='range'],
      input[type='date'],
      input[type='month'],
      input[type='week'],
      input[type='time'],
      input[type='datetime'],
      input[type='datetime-local'],
      input[type='color'] {
        height: 45px;
      }
      button {
        width: 100%;
      }

    }
    input[type='text'],
    input[type='email'],
    input[type='url'],
    input[type='password'],
    input[type='search'],
    input[type='number'],
    input[type='tel'],
    input[type='range'],
    input[type='date'],
    input[type='month'],
    input[type='week'],
    input[type='time'],
    input[type='datetime'],
    input[type='datetime-local'],
    input[type='color'],
    textarea {
      border: 1px solid $secondary-color-500;
      border-radius: 0;
      font-size: $font-size-base;
      line-height: 1em;
      padding: 10px;
      vertical-align: middle;
    }
  }
}

textarea {
  resize: none;
  padding-left: .6em;
  width: 100%;
}

button,
input[type='button'],
input[type='reset'],
input[type='submit'] {
  border: 1px solid;
  border-radius: 0;
  font-family: $secondary-font;
  font-size: $font-size-base;
  line-height: 1.15em;
  padding: 10px;
  vertical-align: middle;
}

.frm_message,
.frm_error_style {
  text-align: center;
  p {
    margin-bottom: 0 !important;
  }
}

/*---- Placeholder ----*/
::-webkit-input-placeholder {
  opacity: 1;
  color: $secondary-color !important;
}
\:-moz-placeholder {
  opacity: 1;
  color: $secondary-color !important;
}
::-moz-placeholder {
  opacity: 1;
  color: $secondary-color !important;
}
\:-ms-input-placeholder {
  opacity: 1;
  color: $secondary-color !important;
}
*::-webkit-input-placeholder {
  opacity: 1;
  color: $secondary-color !important;
}
*:-moz-placeholder {
  opacity: 1;
  color: $secondary-color !important;
}
*:-ms-input-placeholder {
  opacity: 1;
  color: $secondary-color !important;
}

fieldset {
    min-width: 0;
}